<template>
  <div class="main-cut-box" v-show="url">
    <img :src="url">
    <div class="tool-box">
      <div @click="close">删除</div>
      <div @click="cutImage">裁剪</div>
    </div>
    <!--div class="select-box"></div-->
  </div>
</template>

<script>
    export default {
        name: "Cut",
        props: ['url'],
        methods: {
            close() {
                this.$emit('close')
            },
            cutImage() {
                alert('还未实现');
            }
        }
    }
</script>

<style scoped>
  .main-cut-box {
    display: block;
    height: auto;
    position: relative;
    overflow: hidden;
  }

  img {
    width: 100%;
    height: auto;
  }

  .tool-box {
    position: absolute;
    top: 5px;
    right: 0;
    flex-direction: row-reverse;
    display: flex;
    width: 100%;
    transform: translateY(-50px);
    transition: all 0.3s;
  }

  .main-cut-box:hover .tool-box {
    transform: translateY(0);
  }

  .main-cut-box:hover .tool-box::before {
    content: '';
    display: block;
    height: 10px;
    width: 100%;
    position: absolute;
    top: -15px;
    left: 0;
    box-shadow: 0 20px 70px 20px rgba(0,0,0,0.3);
  }

  .tool-box >* {
    color: #fff;
    margin: 5px 10px;
    cursor: pointer;
    z-index: 9;
    padding: 5px 12px;
  }

  .tool-box >*:hover {
    border-left: solid 1px rgba(255,255,255,0.5);
    border-right: solid 1px rgba(200,200,200,0.5);
    padding: 5px 11px;
  }

  .select-box {
    border: solid 4px rgba(255,255,255,0.7);
    position: absolute;
    top: 140px;
    left: 40px;
    width: 40px;
    height: 50px;
    resize: both;
    z-index: 99;
    overflow: auto;
  }

  .select-box:hover {
    border: solid 4px rgba(255,255,255,1);
  }
</style>



