<template>
    <div class="color-box">
      <div v-for="(item, index) in colors" class="item-box"  @click="select(index)">
        <div :class="selected===index?'color-item color-selected':'color-item color-unselected'" :style="{'background-color':item}"></div>
        <div v-if="label" class="color-label">{{label[index]}}</div>
      </div>
      <div v-if="diy" class="item-box">
        <div  :class="selected===-2?'color-item color-selected':'color-item color-unselected'" :style="{'background-color':diyColor}" @click="clickDIY"></div>
        <input @change="colorChange" placeholder="自定义"/>
      </div>
    </div>
</template>

<script>
    export default {
        name: "ColorSelector",
        props: ['value', 'colors', 'items', 'label', 'diy'],
        data() {
            return {
                selected: this.items.indexOf(this.value),
                diyColor: null
            }
        },
        methods: {
            select(index) {
                this.selected = index;
                if (this.items) {
                    this.$emit('input', this.items[index]);
                } else {
                    this.$emit('input', index);
                }
            },
            colorChange(e) {
                this.selected = -2;
                this.diyColor = e.target.value;
                this.$emit('input', this.diyColor);
            },
            clickDIY() {
                this.selected = -2;
                this.$emit('input', this.diyColor);
            }
        }
    }
</script>

<style scoped>
  .color-box {
    width: 100%;
    height: 30px;
    display: flex;
    justify-content: space-between;
  }

  .item-box {
    text-align: left;
    cursor: pointer;
    background-color: #f5f5f5;
    border-radius: 15px;
    display: inline-block;
  }

  .color-item {
    width: 28px;
    height: 28px;
    display: inline-block;
    vertical-align: top;
    border-radius: 15px;
  }

  .color-unselected {
    border: solid 1px #ddd;
  }
  .color-selected {
    border: solid 3px #28e08c;
    width: 24px;
    height: 24px;
  }

  .color-label {
    white-space: nowrap;
    display: inline-block;
    vertical-align: top;
    height: 30px;
    line-height: 30px;
    padding-right: 10px;
    text-align: left;
    font-size: medium;
  }

  input {
    width: 50px;
    height: 28px;
    outline: none;
    border: none;
    font-size: medium;
    background: none;
    vertical-align: top;
    display: inline-block;
    padding-right: 5px;
  }

  @media screen and (max-width : 550px) {
    .color-box {
      display: block !important;
      height: auto;
      text-align: left;
    }

    .item-box {
      width: calc(100% / 3);
      background: none;
      margin-top: 20px;
    }
  }
</style>



