<template>
  <div class="main-switch">
    <div class="switch-label" v-if="label">{{label}}</div>
    <div :class="value?'switch-box bg-green':'switch-box bg-dark'" @click="turnIt">
      <div :class="value?'switch-button on':'switch-button off'"></div>
    </div>
  </div>
</template>

<script>
    export default {
        name: "Turn",
        props: ['value', 'label'],
        data() {
            return {
            }
        },
        methods: {
            turnIt() {
                this.value = !this.value;
                this.$emit('input', this.value);
            }
        }
    }
</script>

<style scoped>
  .main-switch {
    display: inline-block;
    text-align: left;
    margin-right: 10px;
  }

  .switch-box {
    width: 60px !important;
    height: 30px !important;
    border-radius: 15px;
    position: relative;
    overflow: hidden;
    cursor: pointer;
    display: inline-block;
    vertical-align:top;
    transition: all 0.2s;
  }

  .switch-label {
    margin-right: 10px;
    line-height: 30px;
    height: 30px;
    color: #777;
    display: inline-block;
    vertical-align:top;
    white-space: nowrap;
    overflow: hidden;
    margin-bottom: 5px;
    font-size: medium;
  }

  .switch-button {
    border-radius: 50%;
    width: 24px;
    height: 24px;
    position: absolute;
    top: 3px;
    transition: left 0.2s;
    background-color: #f9f9f9;
  }
  .bg-green {
    background-color: #28e08c;
  }
  .bg-dark {
    background-color: #e5e5e5;
  }

  .on {
    left: 33px;
  }
  .off {
    left: 3px;
  }

  @media screen and (max-width: 550px) {
    .main-switch {
      display: block !important;
      margin-top: 30px;
    }
    .switch-box {
      float: right;
    }
    .switch-label {
      color: #000;
    }
  }
</style>



