<template>
  <div id="app">
    <div class="content"><keep-alive><router-view/></keep-alive></div>
  </div>
</template>

<script>
  import {throttle} from "./lib/throttle";

  export default {
    name: 'App',
      data() {
        return {
        }
      },
      methods: {
      },
      created() {
        let position;
        let ansys = (end ,position) => {
            if (end[0] - position[0] > 50 && Math.abs(end[1] - position[1]) < 100) {
                console.log('slide right');
            }
            if (end[0] - position[0] < -50 && Math.abs(end[1] - position[1]) < 100) {
                console.log('slide left');
            }
            if (end[1] - position[1] > 50 && Math.abs(end[0] - position[0]) < 100) {
                console.log('slide down');
            }
            if (end[1] - position[1] < -50 && Math.abs(end[0] - position[0]) < 100) {
                console.log('slide top');
            }
        };
        let th_ansys = throttle(ansys, 300);
        document.addEventListener('touchstart', (e) => {
            position = [e.touches[0].clientX, e.touches[0].clientY];
            document.addEventListener('touchend', (e) => {
                let end = [e.changedTouches[0].clientX, e.changedTouches[0].clientY];
                th_ansys(end, position);
                document.removeEventListener('touchend', () => {}, false);
            }, false)
        }, false);
      }
  }

  const touchEvent = {
      left: {
          loop: {},
          add(event) {

          }
      }
  }
</script>

<style>
  a {
    text-decoration: none;
  }
  body, html {
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    margin: 0;
    border: none;
    padding: 0;
  }
  body{ -webkit-tap-highlight-color:rgba(0,0,0,0); }
  a:focus,
  input:focus,
  p:focus,
  div:focus{
    -webkit-tap-highlight-color:rgba(0,0,0,0);
    outline: none;
    border: none;
  }

  * {
    -webkit-overflow-scrolling: touch;
  }

  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    height: 100vh;
    width: 100vw;
    overflow: hidden;
    margin: 0;
    border: 0;
    display: flex;
  }
  .side-box {
    margin: 0;
    width: 200px;
    height: 100%;
    background-color: #f7f7f7;
    border-right: solid 1px #ddd;
    position: relative;
  }
  .side-box .side-avatar {
    width: 60%;
    max-width: 90px;
    max-height: 90px;
    margin-top: 20px;
    margin-bottom: 20px;
    border-radius: 50%;
    border: solid 5px #ddd !important;
    transition: all 0.2s;
    cursor: pointer;
  }
  .side-box .side-avatar:hover {
    border: solid 5px #ccc !important;
  }
  .side-item {
    overflow: hidden;
  }
  .side-item >div {
    width: calc(100% - 40px);
    height: 50px;
    margin: 0 auto;
    padding-right: 20px;
    padding-left: 20px;
    line-height: 50px;
    text-align: left;
    color: #000;
    display: inline-block;
    font-weight: bold;
  }
  .side-item >div:hover {
    background: linear-gradient(to right, #eee 50%, #ddd 100%);
    background-size: 200%;
    background-position: right 0;
  }
  .selected >div {
    background: linear-gradient(to right, #eee 50%, #ccc 100%);
    background-size: 200%;
    background-position: right 0;
  }
  .side-item-icon {
    width: 25px;
    height: 25px;
    float: left;
    margin: 12.5px;
    margin-left: 0;
  }
  .content {
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
  }

  .hide-aside {
    height: 0 !important;
  }
  @media only screen and (max-width: 550px) {
    .content {
      position: fixed;
      height: 100vh;
      width: 100vw;
      top: 0;
      left: 0;
    }
  }
</style>



