export const throttle = function(thing, time = 1000) {
  let lock = false;
  let timer;
  return function() {
    if (!lock) {
      thing.apply(this, arguments);
      lock = true;
      timer = setTimeout(() => {
        lock = false;
        clearTimeout(timer);
        timer = null;
      }, time)
    }
  }
}



