}
.light-star {
  background: linear-gradient(125deg, #444, #999);
  color: #fff;
}
.dark-star {
  background-color: #f5f5f5;
  color: #aaa;
}

.form-active {
  height: 60%;
  box-shadow: -20px 0 20px rgba(0, 0, 0, 0.4);
}
.form-hide {
  height: 30px;
}
.form-hide form * {
  visibility: hidden;
}
.form-hide .bar {
  visibility: visible !important;
}

.num1 > * {
  width: 100%;
}
.num2 > * {
  width: calc(49% - 10px);
}
.num3 > * {
  width: calc(33% - 10px);
}
.num4 > * {
  width: calc(24% - 10px);
}

form::-webkit-scrollbar {
  display: none;
}

input[type="range"] {
  -webkit-appearance: none;
  border: none;
  display: block;
  margin-top: 20px;
}
input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  border: none;
}
input[type="range"]::-webkit-slider-runnable-track {
  height: 1px;
  background: #ddd;
}
input[type="range"]::-moz-range-track {
  height: 1px;
  background: #ddd;
}
input[type="range"]:focus {
  outline: none;
  border: none;
}
input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 20px;
  width: 20px;
  border-radius: 10px;
  background: #ccc;
  transform: translateY(-10px);
}
input[type="range"]::-moz-range-thumb {
  -webkit-appearance: none;
  height: 20px;
  width: 20px;
  border-radius: 10px;
  background: #ccc;
  border: none;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  appearance: none;
  margin: 0;
}
input {
  -moz-appearance: textfield;
}

.ocgwiki {
  color: blue;
  cursor: pointer;
}

::-webkit-input-placeholder {
  /* WebKit browsers */
  color: #aaa;
  font-size: small;
}

::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: #aaa;
  font-size: small;
}

:-ms-input-placeholder {
  /* Internet Explorer 10+ */
  color: #aaa;
  font-size: small;
}

aside > ul li {
  cursor: pointer;
}

aside > ul li:active {
  background-color: #ddd;
}

.canvas-card {
  box-shadow: 30px 30px 100px rgba(0, 0, 0, 0.4);
}

section2 {
  display: block !important;
}

@media screen and (min-width: 550px) {
  .main-box {
    display: flex;
  }
  .card-box {
    height: 100%;
    width: 50%;
    display: flex;
    overflow: hidden;
  }
  .form-box {
    width: calc(50% - 100px);
    padding: 10px 50px;
    overflow-y: auto;
    display: block;
    z-index: 888;
    left: auto;
    right: 0;
    top: 0;
    box-shadow: 0 0 0 #fff;
    position: relative;
    transition: all 0s;
    height: 100%;
    background-color: rgba(255, 255, 255, 0.7);
  }
  .form-box * {
    font-size: large;
    visibility: visible !important;
  }
  form {
    padding-top: 40px;
    overflow: visible;
  }

  nav {
    position: fixed;
    width: 50%;
    margin-left: 50%;
    background-color: #f5f5f5;
  }

  .li-unactivate {
    border-bottom: solid 3px #f5f5f5;
  }

  aside {
    left: auto;
    right: 0;
  }

  .aside-hide {
    transform: translateX(100%);
  }

  .aside-show {
    box-shadow: 0 10px 100px rgba(0, 0, 0, 0.2);
  }

  aside > ul li:hover {
    background-color: #ddd;
  }

  nav input {
    background-color: #f5f5f5;
  }
  nav ul {
    background-color: #f5f5f5;
  }

  section2 {
    display: flex !important;
    justify-content: space-between;
    width: 100%;
  }
}
</style>



