<template>
    <div class="main-box" :class="hide?'hide':'show'">
      <div class="message" @click.stop>
        <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" width="60px" height="60px" viewBox="0 0 50 50" style="enable-background:new 0 0 50 50" xml:space="preserve">
          <path fill="#FF6700" d="M43.935,25.145c0-10.318-8.364-18.683-18.683-18.683c-10.318,0-18.683,8.365-18.683,18.683h4.068c0-8.071,6.543-14.615,14.615-14.615c8.072,0,14.615,6.543,14.615,14.615H43.935z" transform="rotate(275.098 25 25)">
            <animateTransform attributeType="xml" attributeName="transform" type="rotate" from="0 25 25" to="360 25 25" dur="1.2s" repeatCount="indefinite"></animateTransform>
          </path>
        </svg>
        <p><slot></slot></p>
      </div>
    </div>
</template>

<script>
    export default {
        name: "Loading",
        data() {
            return {
                hide: false
            }
        },
        methods: {
            hideThis() {
                this.hide = true;
            }
        }
    }
</script>

<style scoped>
  .main-box {
    width: 100vw;
    height: 100vh;
    background-color: rgba(0,0,0,0.5);
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 999;
  }
  .message {
    width: 80%;
    height: 200px;
    background-color: rgba(255,255,255,0.7);
    backdrop-filter: blur(20px);
    margin: auto;
    text-align: center;
    border-radius: 20px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .message >svg {
    margin: 0 auto;
  }
  .message >p {
    margin: 0 auto;
    margin-top: 20px;
    height: 40px;
    line-height: 40px;
    font-size: large;
  }

  .hide {
    display: none;
  }
  .show {
    display: flex;
  }

  @media only screen and (min-width: 550px) {
    .message {
      width: 300px;
    }
  }
</style>



